<template>
  <div>
  <title-bar/>
  <v-app id="inspire">
    <v-main class="blue-grey lighten-1">
      <v-container style="max-width: 450px" fill-height>
        <v-layout align-center row wrap>
          <v-flex xs12>
            <v-card
            :loading = "loading">
              <div class="pa-5">
                <h1 style="text-align: center" class="mb-5">Sign up</h1>
                <v-form ref="form" lazy-validation>
                  <v-text-field
                      :rules="rules.idrule"
                      :error-messages="id_dup"
                      label ="ID"
                      v-model="userDTO.uid"                  
                      prepend-icon="mdi-account-alert"
                      required
                      >

                  </v-text-field>

                  <v-text-field
                      :rules="rules.pw_rule"
                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                      :type="show1 ? 'text' : 'password'"
                      prepend-icon="mdi-lock"
                      label="password"
                      v-model="userDTO.password"
                      hint="최소 8글자 이상이어야 합니다."
                      @click:append="show1 = !show1"
                  >
                  </v-text-field>
                  <v-text-field
                      :rules="rules.pwcheck_rule"
                      :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                      :type="show2 ? 'text' : 'password'"
                      prepend-icon="mdi-lock"
                      label="Re-enter password"
                      v-model="validatepassword"
                      @click:append="show2 =! show2"
                  >

                  </v-text-field>
                  <v-text-field
                      :rules="rules.unamerule"
                      prepend-icon="mdi-card-account-details"
                      type="text"
                      label="Name"
                      v-model="userDTO.uname"
                  >
                  </v-text-field>
                  <v-text-field
                      v-model="userDTO.email"
                      :rules="rules.emailrule"
                      :error-messages="email_dup"
                      :append-outer-icon="userDTO.email? 'mdi-send' : none"
                      prepend-icon="mdi-email-check"
                      type="text"
                      label="Email"
                      @click:append-outer="sendEmail"
                  >
                  </v-text-field>

                  <template v-if="check_input === true">
                    <v-text-field
                        :rules="rules.Certification_rule"
                        :append-outer-icon="check_value ? 'mdi-send' : none"
                        :error-messages="certi_dup_fail"
                        :success-messages="certi_dup_ok"
                        v-model="check_value"
                        prepend-icon="mdi-check-outline"
                        type="text"
                        label="Certification Number"
                        @click:append-outer="Cert"
                    />
                  </template>

                  <v-snackbar
                      dense outlined
                      dismissible
                      type="warning"
                      v-model="alerts.email_alert"
                      timeout="3000"
                      color="red accent-1"
                  >
                    인증번호를 발송하였습니다.!
                  </v-snackbar>
                  <v-snackbar
                      dense outlined
                      dismissible
                      type="warning"
                      v-model="alerts.allalert"
                      timeout="3000"
                      color="blue lighten-1"
                  >
                    내용을 다시 확인해 보세요.
                  </v-snackbar>

                  <v-snackbar
                      dense outlined
                      dismissible
                      type="warning"
                      v-model="alerts.signalert"
                      timeout="3000"
                      color="red lighten-1"
                  >
                    서버통신 에러.
                  </v-snackbar>

                  <v-btn
                      type="button"
                      color="purple lighten-3 text-capitalize"
                      depressed
                      large
                      block
                      dark
                      class="mb-3"
                      @click="submit"
                  >
                    Submit
                  </v-btn>

                </v-form>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
  </div>
</template>

<script>
import {checkEmail, registerUser} from "@/api/http-common";
import { checkId } from "@/api/http-common";
import titleBar from '@/components/titlebar.vue';


export default {
  data () {
    return {
      loading: false,
      //title 로딩바
      userDTO : {
        uid: '',
        password: '',
        uname: '',
        email: '',
      },
      alerts : {
        //모든것을 검증하는
        allalert : false,
        //서버통신 에러시 (500)
        signalert : false,
        //인증번호 알람.
        email_alert: false,
      },
      rules : {
        idrule : [
          v => !!v || '아이디는 필수 입력사항입니다.',
          v => /^[a-zA-z0-9]*$/.test(v) || '아이디는 영문 + 숫자만 입력 가능합니다.',
          v => !(v && v.length >= 15) || '아이디는 15자 이상 입력할 수 없습니다.',
          v => {
            this.idcheck(v)
          }
        ],
        pw_rule:[
          v => !!v || '패스워드는 필수 입력사항입니다.',
          v => !(v && v.length >= 30) || '패스워드는 30자를 넘을 수 없습니다.',
          v => !(v && v.length < 8 ) || '패스워드는 8자를 넘어야합니다.',
        ],
        pwcheck_rule : [
          v => !!v || '패스워드는 필수 입력사항입니다.',
          v => !(v && v.length >= 30) || '패스워드는 30자를 넘을 수 없습니다.',
          v => v === this.userDTO.password || '패스워드가 일치하지 않습니다.',

        ],
        unamerule : [
          v => !!v || '이름은 필수 입력사항입니다.',
          v => !(v && v.length >=20) || '이름은 20자 이상 입력할 수 없습니다.',
          v => !/[0-9]/.test(v) || '이름에는 숫자를 입력할 수 없습니다.',
          v => !/[~!@#$%^&*()_+|<>?:{}]/.test(v) || '이름에는 특수문자를 사용할 수 없습니다.'
        ],
        Certification_rule : [
          v => !!v || '인증번호를 입력하세요!',
        ],
        emailrule : [
          v => !!v || ('이메일은 필수입니다.'),
          v => !v || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || ('email양식을 확인해주세요.'),
        ],
      },
      id_dup: [],
      show1 : false,
      pwvalue : '',
      validatepassword : '',
      show2 : false,
      email_dup:'',
      check_input : false,
      check_value: '',
      Certification_num : '',
      certi_dup_ok : [],
      certi_dup_fail : [],
      certi_result : false,
    }
  },
  methods: {
    submit(){
      //모든값이 제대로 들어갔는지?
      let validate = this.$refs.form.validate();
      console.log(this.certi_result , this.id_dup.length, validate)
      if (this.id_dup.length === 1 )
        validate = false;
      if(validate && this.certi_result){
        this.alerts.allalert = false;
        this.signup()
      }
      else{
        this.alerts.allalert = true;
      }
    },
    signup(){
      this.loading = true
      setTimeout(()=> (this.loading = false),2000)
      registerUser(this.userDTO)
          .then(res =>{
            if(res.data === this.userDTO.uid){
              this.$router.push({name: "SignupComplete"})
            }else{
              this.alerts.signalert = true
            }
          })
          .catch(e =>{
            this.alerts.signalert=true
            console.log(e)
          });

    },
    async idcheck(v){
      await checkId(v)
      .then(res =>{
        //아이디 중복
        let data = String(res.data)
        if(data === "true") {
          this.id_dup = ['아이디가 중복입니다.']
        }else{
          this.id_dup=[]
        }
      }).catch(e =>{
        console.log(e.message)
      })
    },
    async sendEmail(){
      await checkEmail(this.userDTO.email)
      .then(res => {
        this.alerts.email_alert = true
        let data = String(res.data )
        if(res.status !== 200){
          this.email_dup = ['이메일이 유효하지 않습니다.']
        }else{
          this.check_input = true
          this.Certification_num = data
          this.email_dup=[]
        }
      }).catch(e => {
        console.log(e.message)
          })
    },
    Cert(){
      if(this.Certification_num === this.check_value){
        console.log("COLLECT")
        this.certi_dup_fail=[]
        this.certi_dup_ok = ['인증이 완료되었습니다.']
          this.certi_result = true
      }else{
        console.log("FAIL")
        this.certi_dup_fail = ['인증이 실패하였습니다.']
        this.certi_result = false
      }
    }


  },
  components: {
    titleBar,
  },

  mounted() {
  },


}

</script>